	module.exports = {"data":{"markdownRemark":{"html":"","frontmatter":{"title":"Visualizer","date":"April 14, 2018","path":"/visualizer","about":"‘Visualizer’ was born after an R&D process I did with Roey Tsemah and Ronen Tanchum. Initially our goal was to research how we could use the Web Audio API, to analyse incoming signal (music or microphone inputs), and drive geometry deformations in real-time, while keeping performance optimal so it could run on mobile browsers as well.\nAfter the R&D process was done, I went on to design the concept art for this visualizer, using 3D animation and environment design software packages and then implementing these designs in the web using Three.js. This work was made possible thanks to the amazing post “Experiments with Perlin Noise” examples by Jaume Sanchez.","cover":"visualizer.png","credits":"","components":[["code","Javascript, GLSL"],["software","Blender, e-on Vue, Adobe Photoshop"],["3d","Three.js"]],"press":[],"links":[["Demo","https://juniorxsound.github.io/ICM-Fall-2016/3D_Web_Audio_Visualiser/"],["Github","https://github.com/juniorxsound/ICM-Fall-2016/tree/master/3D_Web_Audio_Visualiser"]],"embed":"","tags":["Experiment"],"excerpt":"A 3D web audio sound visulaizer."}}},"pathContext":{}}

